

import React, { lazy,Suspense,Component } from 'react'
import { useSelector, useDispatch } from 'react-redux'
import {
  CHeader,
  CToggler,
  CHeaderBrand,
  CHeaderNav,
  CBadge,
  CButton,
  CButtonGroup,
  CCard,
  CCardBody,
  CCardFooter,
  CCardHeader,
  CCol,
  CProgress,
  CRow,
  CCallout
} from '@coreui/react'
import {
  Card,
  Col,
  Container,
  Row,
  Spinner,
  CardBody,
  CardHeader,
  CardTitle,
  FormGroup,
  Label,
  Button,
  Form,
  Input,
  InputGroupAddon,
  InputGroupText,
  InputGroup,
  Breadcrumb,
  BreadcrumbItem,
} from "reactstrap"
import CIcon from '@coreui/icons-react'

import Carousel from 'react-bootstrap/Carousel'

import { CContainer, CFade } from '@coreui/react'
import  { Redirect } from 'react-router-dom'

import conf from '../../conf.js';
// import { storage } from "../../firebase/firebase";
import $ from 'jquery'
import TheHeaderVisiteur from '../../containers/TheHeaderVisiteur'
import TheFooter from '../../containers/TheFooter'



const loading = (
  <div className="pt-3 text-center">
    <div className="sk-spinner sk-spinner-pulse"></div>
  </div>
)

class NosContacts extends Component {
  constructor(props) {
      super(props)
      this.state = {
        ok:"",
      }
  }
  charging=()=>{
    $("#msg").css("display","none")
    $("#chargement").css("display","none")
  }
  componentDidMount() {
   this.charging()
  };

  handleSendMessage=()=>{
        $("#chargement").css("display","block")
      
        $("#initial").css("display","none")

        var data= {
          "name": $("#nom").val(),
          "email": $("#email").val(),
          "subject":$("#objet").val(),
          "message":$("#message").val(),
        
        }
        
        const requestOptions = {
          method: 'POST',
          headers: { 'Content-Type': 'application/json','Accept': 'application/json',
        
        },
          body: JSON.stringify(data)
      };

      fetch(conf.url+"acceuil/user/contacter",requestOptions)
    
      .then((data) => {
        

        $("#chargement").css("display","none")
    
        $("#initial").css("display","none")

        $("#msg").css("display","block")

        $("#nom").val("")
        $("#email").val("")
        $("#subject").val("")
        $("#message").val("")
        
      }
      
      );

    }

 

  render() {

    return (
      <>
        {this.state.ok}
        <CContainer fluid>
          <Suspense fallback={loading}>
            <CFade>
           

            <div class="container">
            <div class="section-title">
              <h2>Contact</h2>
            </div>
            <section id="contact" class="contact">
              <div class="container">

                <div class="section-title">
                  <h2>Contact</h2>
                  {/* <p>Magnam dolores commodi suscipit. Necessitatibus eius consequatur ex aliquid fuga eum quidem. Sit sint consectetur velit. Quisquam quos quisquam cupiditate. Et nemo qui impedit suscipit alias ea. Quia fugiat sit in iste officiis commodi quidem hic quas.</p> */}
                </div>

                <div class="row">

                  <div class="col-lg-5 d-flex align-items-stretch">
                    <div class="info">
                      <div class="address">
                        <i class="icofont-google-map"></i>
                        <h4>Adresse:</h4>
                        <p>{this.state.adresse}</p>
                      </div>

                      <div class="email">
                        <i class="icofont-envelope"></i>
                        <h4>Email:</h4>
                        <p>{this.state.email}</p>
                      </div>

                      <div class="phone">
                        <i class="icofont-phone"></i>
                        <h4>Téléphone:</h4>
                        <p>{this.state.telephone}</p>
                      </div>

                      {/* <iframe 
                        src='https://www.google.com/maps/embed?pb=!1m14!1m8!1m3!1d12097.433213460943!2d-74.0062269!3d40.7101282!3m2!1i1024!2i768!4f13.1!3m3!1m2!1s0x0%3A0xb89d1fe6bc499443!2sDowntown+Conference+Center!5e0!3m2!1smk!2sbg!4v1539943755621'
                        frameborder="0" style="border:0; width: 100%; height: 290px;" allowfullscreen
                      >
                      </iframe> */}

                    </div>

                  </div>

                  <div class="col-lg-7 mt-5 mt-lg-0 d-flex align-items-stretch">
                    <div class="php-email-form">
                      <div class="form-row">
                        <div class="form-group col-md-6">
                          <label for="name">Votre nom</label>
                          <input type="text" name="name" class="form-control" id="nom" data-rule="minlen:4" data-msg="Please enter at least 4 chars" />
                          <div class="validate"></div>
                        </div>
                        <div class="form-group col-md-6">
                          <label for="name">Votre Email</label>
                          <input type="email" class="form-control"  id="email" data-rule="email" data-msg="Please enter a valid email" />
                          <div class="validate"></div>
                        </div>
                      </div>
                      <div class="form-group">
                        <label for="name">Objet</label>
                        <input type="text" class="form-control" id="subject" id="objet" data-rule="minlen:4" data-msg="Please enter at least 8 chars of subject" />
                        <div class="validate"></div>
                      </div>
                      <div class="form-group">
                        <label for="name">Message</label>
                        <textarea class="form-control" id="message" rows="10" data-rule="required" data-msg="Please write something for us"></textarea>
                        <div class="validate"></div>
                      </div>
                      <div class="mb-3">
                        <div class="loading">Chargement</div>
                        <div class="error-message"></div>
                        <div class="sent-message">Votre message a été envoyé. Merci !</div>
                      </div>
                      <div id="initial" class="text-center"><button onClick={this.handleSendMessage} id="submit">Envoyer</button></div>
                      <div id="chargement" class="text-center"><button id="submit"><span className="spinner-border spinner-border-sm" role="status" aria-hidden="true"> </span> Envoi...</button></div>
                      <div id="msg" class="sent-message">Votre message a été envoyé. Merci !</div>
                    </div>
                  </div>

                </div>

              </div>
            </section>

          </div>
            </CFade>
          </Suspense>
        </CContainer>
      </>
    )
  }
}

export default NosContacts
